<template>
  <v-container
    id="card-container"
    fluid
  >
    <v-card
      class="mx-auto pb-16"
      max-width="700"
      elevation="0"
    >
      <v-card
        v-for="(cart, index) in $store.state.carts"
        :key="index"
        elevation="1"
        class="ma-3"
      >
        
          <v-img
            :src="$store.state.foods[getFoodItemIndexByID(cart.foodID)].photo"
            max-height="130"
          >
          </v-img>
        
        <v-card-title>
          {{ $store.state.foods[getFoodItemIndexByID(cart.foodID)].name }}
        </v-card-title>
        <v-card-subtitle>
          Số lượng: {{ cart.quantity }}, tổng giá: {{ $store.state.foods[getFoodItemIndexByID(cart.foodID)].price * cart.quantity }} VND
        </v-card-subtitle>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-card
            elevation="0"
          >
            <v-card-actions>
              <v-btn
                icon
                @click="$store.commit('decreaseCartItemQuantity', { index: index, amount: 1 })"
              >
                <v-icon>mdi-minus</v-icon>
              </v-btn>
              <v-btn
                icon
                @click="$store.commit('increaseCartItemQuantity', { index: index, amount: 1 })"
              >
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </v-card-actions>
          </v-card>
          <v-btn
            text
            color="red"
            @click="$store.commit('removeItemFromCart', index)"
          >
            Remove
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-card>

    <v-footer
      v-if="$store.state.carts.length >= 0"
      fixed
    >
      <v-row
        justify="center"
      >
        <v-col>
          <v-dialog
            v-model="isShowPaymentDialog"
          >
            
            <v-card>
              <v-card-title>
                Online Payment
                <v-spacer></v-spacer>
                <v-btn
                  right
                  absolute
                  icon
                  @click="isShowPaymentDialog = false"
                >
                  <v-icon>mdi-close</v-icon>
                </v-btn>
              </v-card-title>
              <v-card-subtitle>
                Enter your FakeMomo's account
              </v-card-subtitle>
              <v-card-text>
                <v-row>
                  <v-col>
                    <v-text-field
                      label="Username"
                      color="orange"
                    >
                    </v-text-field>
                  </v-col>
                </v-row>
              </v-card-text>
              <v-card-actions>
                <v-btn
                  text
                  block
                  color="green"
                >
                  Make payment
                </v-btn>
              </v-card-actions>
            </v-card>
            
            <template
              v-slot:activator="{ on, attrs }"
            >
              <v-btn
                text
                block
                color="orange"
                v-on="on"
                v-bind="attrs"
                :disabled="$store.state.carts.length === 0"
              >
                Online Payment
              </v-btn>
            </template>
          </v-dialog>
        </v-col>
      </v-row>
    </v-footer>
  </v-container>
</template>

<script>
export default {
  methods: {
    getFoodItemIndexByID(id) {
      return this.$store.state.foods.findIndex(item => item.id === id)
    }
  },
  data: () => ({
    isShowPaymentDialog: false
  })
}
</script>

